<template>
  <div >
    <cms-breadcrumb to="/productMange/list" type='list' class="pos-breadcrumb">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="main-layout">
      <div class="fixed-bar">
        <cms-prompt class="pos-prompt" collapse='false'>
          <li>查看商品信息 </li>
        </cms-prompt>
      </div>
      <div class="product-layout" id="productLayout">
         <keep-alive>
             <component :is="currentView"></component>
         </keep-alive>
      </div>
    </div>

  </div>
</template>

<script>
import step1 from './step1'
import step2 from './step2'
export default {
  components: {
    step1: step1,
    step2: step2
  },
  data() {
    return {

    }
  },
  computed: {
    currentView() {
      return this.$store.state.product.step
    }
  },
  methods: {
    setStep(step) {
      this.$store.dispatch('setStep', step)
    }
  },
  activated() {
    if (this.$route.query.type === 'update') {
      this.$store.dispatch('setStep', 'step2')
    }
  }
}
</script>

<style <style lang="scss" scoped>
</style>





