      </el-form-item>
      <h3 class="cms-segmentation">商品相册</h3>
      <el-form-item label="商品相册" prop="imageResources" ref='imageResources' :error='errorMessage.imageResources' class="imageResource">
        <div class="clearfix">
            <div class="product-img-box" v-for="(item,index) in formData.imageResources" :key="index" 
             :preview='false' :index='index' >
              <div class="product-img-warpper">
                <img v-if="formData.imageResources[index]&&formData.imageResources[index].url!==''" 
                :src="$cms.filterUrl(formData.imageResources[index].url)" >
                <span v-else class="img-plus el-icon-plus"></span>
              </div>
            </div>
        </div>
        <span class="gray" style="max-width:490px;">
          用于显示在商品详情页介绍图，最多可支持7张，第一张默认为商品主图用于显示商品列表
        </span>
      </el-form-item>
      <el-form-item label="顶部版式" prop="roleName">
        <span>{{formData.txt.topTplId?formData.txt.topTplId:'未设置'}}</span>
        <span class="gray">
          选中的版式将插入至PC端商品详情最顶部
        </span>
      </el-form-item>
      <el-form-item label="底部版式" prop="roleName">
        <span>{{formData.txt.bottomTplId?formData.txt.bottomTplId:'未设置'}}</span>
        <span class="gray">
          选中的版式将插入至PC端商品详情最底部
        </span>
      </el-form-item>
      <h3 class="cms-segmentation">其他信息</h3>
      <el-form-item label="商品单位">
        <span>{{formData.ext.unit}}</span>
      </el-form-item>
      <el-form-item label="商品服务">
        <span>{{formData.ext.storeServiceId?formData.ext.storeServiceId:'未选择'}}</span>
      </el-form-item>
       <el-form-item label="是否为销售商品出售">
        <el-switch disabled v-model="formData.product.sellGoodsSale"></el-switch>
      </el-form-item>
      <el-form-item label="是否为提供发票">
        <el-switch disabled v-model="formData.product.isInvoice"></el-switch>
      </el-form-item>
      <el-form-item label="商品关键词" prop="ext.keyword">
        <span>{{formData.ext.keyword}}</span>
        <span class="gray">设置检索关键词,用于商品搜索,最多支持5个</span>
      </el-form-item>
      <el-form-item label="评价标签" prop="ext.evaluation">
        <span>{{formData.ext.evaluationView?formData.ext.evaluationView:'暂无'}}</span>
        <span class="gray">买家评价商品时，可对商品选择对应标签进行评价，如性价比高，最多支持10个关键词
        </span>
      </el-form-item>
    </el-form>
    <el-form :model="formData2" label-width="210px">
      <el-form-item label="售价">
        <span >{{ $cms.float(formData2.coreModel.salePrice)}}</span>
        <span class="gray">价格控制在0.01~99999999之间的数字，且不高于市场价，如商品设置SKU，此价格自动记录为最低SKU价格</span>
      </el-form-item>
      <el-form-item label="市场价">
        <span >{{ $cms.float(formData2.coreModel.marketPrice)}}</span>
      </el-form-item>
      <el-form-item label="成本价">
        <span >{{ $cms.float(formData2.coreModel.costPrice)}}</span>
      </el-form-item>
      <el-form-item label="库存量">
        <span >{{formData2.coreModel.inventory}}</span>
        <span class="gray">库存量控制在1~99999999之间的整数，如商品设置SKU，库存量自动计算为所有SKU库存总和 </span>
      </el-form-item>
      <el-form-item label="库存预警值">
        <span >{{formData2.coreModel.warningValue}}</span>
        <span class="gray">库存预警值必须少于库存量，如商品设置SKU，预警值自动计算为所有 SKU设定的预警值总和
        </span>
      </el-form-item>
      <el-form-item label="商品规格" v-if="formData2.coreModel.hasSku">
        <!-- 规格信息获取 -->
        <el-checkbox-group disabled  v-model="checkSaleStandardIds" v-if="formData2.coreModel.hasSku">
          <section class="standard-group" v-for="(group,index) in saleStandardInfos" :key="group.id">
            <div class="standard-group-header" >
              <div class="fl">
                <span class="standard-group-name">{{group.name}}</span>
                <span class="standard-group-desc" v-if="group.valueType==4">( 该规格需要上传图片 )</span>
              </div>
            </div>
            <div class="standard-group-body">
              <!-- 判断下是图片还是复选类型 -->
              <ul v-if="group.valueType===4" class="clearfix">
                <li v-for="(item,index2) in group.attrValueSet" :key="item.attrCode" class="type-img-items">
                  <div v-show="checkSaleStandardIds.indexOf(item.attrCode) >-1" :preview='false' :index='index2' :params="{parentIndex:index,attrCode:item.attrCode}" class="product-img-box">
                    <div class="product-img-warpper">
                      <img v-if="standardImageUrls[item.attrCode] && standardImageUrls[item.attrCode] !==''"
                       :src="$cms.filterUrl(standardImageUrls[item.attrCode])" alt="">
                      <span v-else class="img-plus el-icon-plus"></span>
                    </div>
                  </div>
                  <div class="img-txt">
                    <el-checkbox  v-show="checkSaleStandardIds.indexOf(item.attrCode) >-1" disabled :label="item.attrCode" class="check-box">
                      <span class="type-value">{{item.attrValue}}</span>
                    </el-checkbox>
                  </div>
                </li>
              </ul>
              <ul v-else class="clearfix">
                <li v-for="(item) in group.attrValueSet" :key="item.attrCode" class="type-checkbox-items">
                  <el-checkbox v-show="checkSaleStandardIds.indexOf(item.attrCode) >-1" disabled :label="item.attrCode">
                    <span class="type-value">{{item.attrValue}}</span>
                  </el-checkbox>
                </li>
              </ul>
            </div>
          </section>
        </el-checkbox-group>
      </el-form-item>
      <!-- sku信息 -->
      <el-form-item label="sku信息" prop="name" v-if='formData2.coreModel.hasSku&&skus.length>0'>
        <table class="sku-table">
          <tr>
            <th v-for="(item,index) in properties" :key="index"  rowspan='2'>
              {{item.name}}
            </th>
            <th>售价</th>
            <th>市场价</th>
            <th>成本价</th>
            <th>库存</th>
            <th>预警值</th>
          </tr>
          <tr>
            <td>
              <span>{{batch.salesPrice}}</span>
            </td>
            <td>
              <span>{{batch.marketPrice}}</span>
            </td>
            <td>
              <span>{{batch.costPrice}}</span>
            </td>
            <td>
              <span>{{batch.inventory}}</span>
            </td>
            <td>
              <span>{{batch.warning}}</span>
            </td>
          </tr>
          <tr v-for="(item,index2) in skus" :key="index2">
            <td v-for="(name,index3) in item.name" :key="index3" class="min-w-90">
              {{name}}
            </td>
            <td>
              <span>{{item.salesPrice}}</span>
            </td>
            <td>
              <span>{{item.marketPrice}}</span>
            </td>
            <td>
              <span>{{item.costPrice}}</span>
            </td>
            <td>
              <span>{{item.inventory}}</span>
            </td>
            <td>
              <span>{{item.warning}}</span>
            </td>
          </tr>
        </table>
        <span class="gray">注：商品实际销售SKU不存在的组合，可删除，系统将默认保存为空</span>
      </el-form-item>
      <!-- 商品属性信息 -->
      <el-form-item label="商品属性" prop="name">
        <section class="standard-group" v-for="(group,index) in productStandardInfos" :key="index">
          <div class="standard-group-header">
            <div class="fl">
              <span class="standard-group-name">{{group.attrGroupName}}</span>
            </div>
          </div>
          <div class="standard-group-body" style="padding:20px;">
            <!-- 属性组里面的属性项遍历 -->
            <div class="clearfix" :class="[standard.valueType==1?'w-50':'w-100']" v-for='(standard,index2) in group.attrs' :key="index2">
              <label class="fl  attribute-name">{{standard.name}}</label>
              <!-- 多选 -->
              <ul class="fl" v-if="standard.valueType===3 && standard.attrValueSet.length > 0">
                <el-checkbox-group disabled v-model="attributeGroups[standard.id+'s']">
                <li v-for="(item) in standard.attrValueSet" :key="item.id" class="type-checkbox-items">
                  <el-checkbox v-show="attributeGroups[standard.id+'s'] === item.attrValue" disabled :label="item.attrCode">
                    <span class="type-value">{{item.attrValue}}</span>
                  </el-checkbox>
                </li>
                </el-checkbox-group>
              </ul>
              <!-- 单选 -->
              <ul class="fl" v-if="standard.valueType===2 && standard.attrValueSet.length > 0">
                <!-- 加一个字符串让他变成对象0.0 -->
                <el-radio-group disabled v-model="attributeGroups[standard.id+'s']">
                  <li v-for="(item) in standard.attrValueSet" :key="item.id" class="type-radio-items">
                    <el-radio  v-show="attributeGroups[standard.id+'s'] === item.attrValue" :label="item.attrCode">
                      <span class="type-value">{{item.attrValue}}</span>
                    </el-radio>
                  </li>
                </el-radio-group>
              </ul>
              <!-- 文本 -->
              <ul v-if="standard.valueType===1">
                <li v-for="(item) in standard.attrValueSet" :key="item.id" class="type-radio-items">
                    <span v-show="attributeGroups[standard.id+'s'] === item.attrValue" class="type-value">{{item.attrValue}}</span>
                </li>
              </ul>
            </div>
          </div>
        </section>
      </el-form-item>
      <h3 class="cms-segmentation">详细信息</h3>
      <el-form-item :label="txtModel==='pc'?'PC端':'移动端'">
        <span></span>
      </el-form-item>
      <el-form-item label="商品详情" v-show="txtModel=='pc'">
        <p v-html="formData.txt.pcTxt"></p>
      </el-form-item>
      <el-form-item label="商品详情" v-show="txtModel=='mobile'">
        <p v-html="formData.txt.mobileTxt"></p>
      </el-form-item>
    </el-form>
    <!-- 商品下架原因 -->
    <el-dialog title="商品下架" top="35vh" :visible.sync='dialogVisible' width="580px">
      <div class="dialog-form">
        <el-form :model="form">
          <el-form-item label="SPU编号" label-width="120px">
            <span class="dialog-info">{{form.code}}</span>
          </el-form-item>
          <el-form-item label="商品名称" label-width="120px">
            <span class="dialog-info"> {{form.name}} </span>
          </el-form-item>
          <el-form-item label="下架说明" label-width="120px">
            <el-input maxlength="150" v-model="form.data" type='textarea' :rows="4" placeholder="请输入最多150字的下架说明"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" size="mini" @click="productOffShelve">确认</el-button>
        <el-button type='info' size='mini' @click.native="dialogVisible=false">取消</el-button>
      </div>
    </el-dialog>
     <!-- 商品审核不通过原因 -->
    <el-dialog title="商品审核" top="35vh" :visible.sync='dialogVisible2' width="580px">
      <div class="dialog-form">
        <el-form :model="form2">
          <el-form-item label="SPU编号" label-width="120px">
            <span class="dialog-info">{{form2.code}}</span>
          </el-form-item>
          <el-form-item label="商品名称" label-width="120px" >
            <span class="dialog-info"> {{form2.name}} </span>
          </el-form-item>
          <el-form-item label="审核说明" label-width="120px">
            <el-input maxlength="150" v-model="form2.data" type='textarea' :rows="4" placeholder="请输入最多150字的审核说明"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" size="mini" @click="productReviewReject">确认</el-button>
        <el-button type='info' size='mini' @click.native="dialogVisible2=false">取消</el-button>
      </div>
    </el-dialog>
    <div class="product-footer" :class="[fixed?'fixed-footer':'']">
      <cms-button v-if="btnStatus == 3" icon='down' size='small' type='info' @click.stop="showDialog()">下架</cms-button>
      <cms-button v-if="btnStatus == 1 || btnStatus ==2" icon='up' size='small' type='info' @click.stop="productReviewPass()">上架</cms-button>
      <el-popover width="130" trigger="hover" :visible-arrow="false" :offset='45' popper-class="table-popover">
        <cms-button  v-if="btnStatus == 4" type='info' size='small' icon='lock' slot="reference">审核</cms-button>
        <cms-button type='info' size='small' icon='success' @click.stop="productReview()">审核通过</cms-button>
        <cms-button type='info' size='small' icon='warning' @click.stop="showDialogDis()">审核不通过</cms-button>
      </el-popover>
      <cms-button v-if="btnStatus == 5" icon='down' size='small' type='info' @click.stop="reStoreProduct()">还原</cms-button>
    </div>
  </div>
</template>
  <script>
import { productCategoryGet } from '@/api/product/category.js'
import { storeCategoryTree } from '@/api/shop/shopManage.js' // 商家商品分类
import { productGet, productOffShelve, productReviewPass, productRestore, productReviewReject } from '@/api/product/product.js'
var defaultData = {
  product: {
    storeId: '',
    storeCategoryId: '',
    categoryId: '',
    name: '',
    virtualCardTplId: '',
    itemNumber: '',
    brandId: 1,
    summary: '',
    summaryColor: '#ff0000',
    templeteId: 1,
    weight: '',
    volume: '',
    sellGoodsSale: true,
    isInvoice: true,
    status: 3
  },
  productTypeId: '',
  storeCategoryIds: [],
  imageResources: [
    // 最多五张图片
    {
      remark: '',
      imgResourceId: '',
      url: ''
    }
  ],
  videoResources: [
    {
      remark: '',
      vdResourceId: '',
      url: ''
    }
  ],
  txt: {
    pcTxt: 'pc端内容',
    mobileTxt: '移动端内容'
  },
  ext: {
    maxDeductible: 0,
    unit: '件',
    keyword: '',
    evaluation: '',
    storeServiceId: ''
  }
}
var defaultForm = {
  coreModel: {
    salePrice: '',
    marketPrice: 0.0,
    costPrice: 0.0,
    inventory: 0,
    warningValue: 0,
    hasSku: false
  },
  skuTypeAttrs: [],
  skus: [],
  proTypeAttrs: []
}
var categoryNames = []
export default {
  data() {
    return {
      btnStatus: this.$route.query.status,
      dialogVisible: false,
      dialogVisible2: false,
      loading: true,
      status: false,
      fixed: true,
      txtModel: 'pc',
      storeItems: [], // 所有店铺列表
      roleList: ['超级管理员', '运营'],
      options: [],
      props: {
        value: 'id',
        label: 'cateName',
        children: 'childs',
        disabled: 'isAuth'
      },
      formData: Object.assign({}, defaultData),
      formData2: Object.assign({}, defaultForm),
      categoryName: '',
      isVirtual: false, // 判断是否是虚拟分类
      errorMessage: {},
      popoverVisible: false,
      popoverForm: {
        name: '',
        attrValues: ''
      },
      storeId: 0, // 这里要取上一步的商城id
      productTypeId: 0, // 这里要取上一步商品类型的id
      loadingTitle: '正在保存,请勿进行其他操作',
      defaultData: {},
      batch: {
        salesPrice: '',
        costPrice: '',
        inventory: '',
        marketPrice: '',
        warning: ''
      },
      customStandardValue: {}, // 自定义规格值
      customAttributeValue: {}, // 自定义属性值
      checkSaleStandardIds: [], // 选中的规格id
      standardImageUrls: {}, // 选中的规格对应图片，对象结构{"规格code":"规格图片地址"}
      properties: [], // 选中的商品规格
      attributes: [], // 选中的属性值
      saleStandardInfos: [], // 销售属性集合
      productStandardInfos: [], // 商品属性集合
      skus: [], // 生成的sku信息
      attributeGroups: {},
      form: {
        code: '',
        name: '',
        data: '',
        id: ''
      },
      form2: {
        status: true,
        code: '',
        name: '',
        data: '',
        id: ''
      }
    }
  },
  methods: {
    showDialog() { // 商品下架
      this.form.data = ''
      this.form.code = this.formData.product.itemCode
      this.form.name = this.formData.product.name
      this.form.id = [this.formData.product.id]
      this.dialogVisible = true
    },
    showDialogDis() {
      this.form2.code = this.formData.product.itemCode
      this.form2.name = this.formData.product.name
      this.form2.id = [this.formData.product.id]
      this.dialogVisible2 = true
    },
    productReviewReject() { // 审核不通过
      productReviewReject({
        ids: this.form.id,
        data: this.form.data
      }).then(res => {
        this.dialogVisible2 = false
        if (res.code === 200) {
          this.$successMessage('操作成功')
          this.$routerLink('/productMange/list')
        } else {
          this.$errorMessage('操作失败')
        }
      })
    },
    productReview() { // 审核通过
      var id = this.formData.product.id
      this.$confirm('是否确定通过审核该商品?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          productReviewPass({ ids: [id] }).then(res => {
            if (res.code === 200) {
              this.$successMessage('审核成功')
              this.$routerLink('/productMange/list')
            } else {
              this.$errorMessage('审核失败,' + res.message)
            }
          })
        })
        .catch(() => {})
    },
    reStoreProduct() {
      var id = this.formData.product.id
      // 还原方法
      this.$confirm('是否确认还原商品?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          productRestore({ ids: [id] }).then(res => {
            if (res.code === 200) {
              this.$successMessage('恢复成功')
              this.$routerLink('/productMange/list')
            } else {
              this.$errorMessage('恢复失败,' + res.message)
            }
          })
        })
        .catch(() => {})
    },
    productReviewPass() {
      var id = this.formData.product.id
      this.$confirm('是否确定重新上架该商品?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          productReviewPass({ ids: [id] }).then(res => {
            if (res.code === 200) {
              this.$successMessage('上架成功')
              this.$routerLink('/productMange/list')
            } else {
              this.$errorMessage('上架失败,' + res.message)
            }
          })
        })
        .catch(() => {})
    },
    productOffShelve() {
      // 下架，批量下架
      productOffShelve({
        ids: this.form.id,
        data: this.form.data
      }).then(res => {
        this.dialogVisible = false
        if (res.code === 200) {
          this.$successMessage('操作成功')
          this.$routerLink('/productMange/list')
        } else {
          this.$errorMessage('操作失败')
        }
      })
    },
    initData() { // 进入加载
      this.loading = true
      productGet({ id: this.$route.query.productId }).then(res => {
        // 初始化商品数据
        this.formData.product = res.data.product
        this.formData.txt = res.data.txt
        this.formData.ext = res.data.ext
        this.setStoreData(res.data)
        this.getProductTypeId(this.formData.product.category.id) // 加载商品类型id
        this.getStroeCategory(res.data.product.storeId) // 手动触发一下分类
        const obj = this.$cms.deepCopy(this.formData)
        obj.product.storeCategoryId =
              obj.storeCategoryIds[obj.storeCategoryIds.length - 1]
        this.$store.dispatch('setBaseProductInfo', obj).then(res => {
          this.initData2()
        })
      })
    },
    initData2() { // 初始化数据,在这里将数据处理完成
      const productInfos = this.$store.state.product.productInfo
      this.storeId = this.$store.state.product.storeId
      this.productTypeId = this.$store.state.product.productTypeId
      this.formData2.coreModel = productInfos.coreModel// 设置基础价格
      this.saleStandardInfos = productInfos.proTypeAttrItems.saleAttrs// 规格列表
      this.productStandardInfos = productInfos.proTypeAttrItems.nsaleAttrs
      this.attributes = productInfos.proAttrs// 设置属性
      this.properties = productInfos.skuTypeAttrs
      this.setDefaultProperties()// 默认选中的规格
      this.setDefaultAttributes(productInfos.proAttrs)
      this.defaultData = productInfos
    },
    setDefaultProperties() { // 设置默认选中的规格项
      this.properties.forEach(v => {
        v.attrValueList.forEach(item => {
          this.checkSaleStandardIds.push(item.attrCode)// 选中的id
          this.standardImageUrls[item.attrCode] = item.url// 选中的图片
        })
      })
    },
    setDefaultAttributes(data) { // 设置选中的属性值
      const obj = {}
      data.forEach(v => {
        if (v.attrValueType === 3) {
          obj[v.proTypeAttrId + 's'] = v.attrVc.split(',')
        } else {
          obj[v.proTypeAttrId + 's'] = v.attrVc
        }
      })
      this.attributeGroups = obj
      return obj
    },
    getStroeCategory(id) {
      // 加载对应的商家商品分类
      storeCategoryTree({ storeId: id }).then(res => {
        this.options = []
        const root = [{ id: 0, cateName: '顶级分类' }]
        this.options = root.concat(res.data)
      })
    },
    setStoreData(data) {
      // 拆解返回的信息,参考文档处理
      const params = this.$cms.deepCopy(data)
      const imgArr = []
      const videoArr = [
        {
          remark: '',
          vdResourceId: '',
          url: ''
        }
      ]
      this.formData.storeCategoryIds = params.product.storeProductCategory
        ? params.product.storeProductCategory.nodeIds
        : [0]
      params.resources.forEach(v => {
        // 拆解视频图片资源集合
        if (v.videoResource && v.videoResource.id !== '') {
          const obj = {
            remark: '',
            alias: v.videoResource.alias,
            vdResourceId: v.videoResource.id,
            url: v.videoResource.url
          }
          videoArr[0] = obj
        } else if (v.imageResouce && v.imageResouce.id !== '') {
          const obj2 = {
            remark: '',
            imgResourceId: v.imageResouce.id,
            url: v.imageResouce.url
          }
          imgArr.push(obj2)
        }
      })
      this.formData.product.categoryId = this.formData.product.category.id // 设置分类id
      params.product.categoryId = params.product.category.id // 设置分类id
      this.getCategoryName(params.product.category) // 设置分类名称
      this.formData.imageResources = Object.assign(
        this.formData.imageResources,
        imgArr
      )
      params.imageResources = Object.assign(
        this.formData.imageResources,
        imgArr
      )
      this.formData.videoResources = videoArr
      params.videoResources = videoArr
      if (params.product.brand && params.product.brand.id !== '') {
        // 设置品牌id
        this.formData.product.brandId = params.product.brand.id
        params.product.brandId = params.product.brand.id
      } else {
        this.formData.product.brandId = ''
        params.product.brandId = ''
      }
      // 设置规格属性
      params.skuTypeAttrs = this.createSkuTypeAttrs(
        params.proTypeAttrs.saleAttrs,
        params.skuTypeAttrs
      )
      params.skus = this.createSku(params.skus)
      params.proTypeAttrItems = params.proTypeAttrs // 转换下属性
      params.proTypeAttrs = params.proAttrs
      delete params.product.category
      delete params.product.brand
      this.$store.dispatch('setProductInfo', params)
      this.status = true // 需要数据加载完成后加载编辑器那些

      return params
    },
    createSku(data) {
      const skus = []
      data.forEach((v, i) => {
        skus.push(v)
        skus[i].attrCodes = v.saleAttrCode.split('-')
      })
      return skus
    },
    createSkuTypeAttrs(saleStandardInfos, data) {
      // 设置规格属性信息
      const properties = []
      saleStandardInfos.map(v => {
        properties.push({
          valueType: v.valueType,
          attrGroupName: v.attrGroupName,
          name: v.name,
          id: v.id,
          attrValueList: []
        })
      })

      data.forEach((v, i) => {
        properties.forEach((item, j) => {
          if (v.productTypeAttr.id === item.id) {
            const obj = {
              url: v.resourceData ? v.resourceData.url : '',
              valueResourceId: v.resourceData ? v.resourceData.id : '',
              attrCode: v.attrValueCode,
              attrValue: v.attrValueName
            }
            item.attrValueList.push(obj)
          }
        })
      })
      return properties
    },

    getCategoryName(obj) {
      // 设置分类名称
      categoryNames.push(obj.cateName)
      if (obj.parent) {
        this.getCategoryName(obj.parent)
      } else {
        const arr = categoryNames.reverse()
        arr.forEach((v, i) => {
          if (i === 0) {
            this.categoryName = v
          } else {
            this.categoryName = this.categoryName + ' > ' + v
          }
        })
      }
    },
    getProductTypeId(categoryId) { // 这里获取配套的列表信息
      productCategoryGet({ id: categoryId }).then(res => {
        this.loading = false
        if (res.code === 200) {
          this.formData.productTypeId = res.data.productTypeId // 设置商品类型id
          this.isVirtual = res.data.virtual // 设置是否是虚拟分类
        } else {
          this.$errorMessage(res.message)
        }
      })
    }
  },
  activated() {
    categoryNames = []
    if (this.$route.query.productId && this.$route.query.productId !== 0) {
      this.initData() // 初始化数据
    }
  },
  mounted() {
    const lay = document.querySelector('#layout')
    lay.addEventListener('scroll', e => {
      let scrollTop = e.target.scrollTop
      const clientHeight = e.target.clientHeight
      const scrollHeigh = e.target.scrollHeight
      if (this.fixed === false) {
        scrollTop += 70
      }
      if (parseInt(scrollTop) > scrollHeigh - clientHeight - 70) {
        this.fixed = false
      } else {
        this.fixed = true
      }
    })
  }
}
</script>
<style lang="scss" scoped>
.w-50 {
  width: 50%;
  display: inline-block;
  margin: 5px 0;
}
.w-100 {
  width: 100%;
  display: block;
}
.attribute-name {
  width: 120px;
  margin-right: 25px;
  font-size: 14px;
  color: #666;
  text-align: right;
}
.icon-zhankai1-copy {
  cursor: pointer;
}
.del{
  cursor: pointer;
  font-size: 14px;
  color: #a3a3a3;
  &:hover{
    color: #fe5f40;
  }
}
</style>




